<template>
  <div id="app">
    <nav>
      <button @click="startNewChat">新建聊天</button>
    </nav>
    <div class="main-container">
      <!-- 左侧历史记录列表 -->
      <aside class="sidebar">
        <h2>历史对话记录</h2>
        <ul>
          <li v-for="(record, index) in chatHistory" :key="index">
            <button @click="loadChatHistory(index)">对话 {{ index + 1 }}</button>
          </li>
        </ul>
      </aside>

      <!-- 右侧聊天窗口 -->
      <router-view :key="activeChatIndex" :messages="activeMessages" @update-messages="updateMessages"/>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chatHistory: [[]], // 初始化第一个对话为空，确保对话1保存初始消息
      activeChatIndex: 0, // 从第一个对话开始
      isLoggedIn: false,
      username: '',
    };
  },
  provide(){
    return {
      isLoggedIn: this.isLoggedIn,
      username: this.username,
      setLogin: this.setLogin,
    };
  },
  computed: {
    activeMessages() {
      // 当前显示的消息，取自当前聊天记录或新建的空对话
      return this.chatHistory[this.activeChatIndex];
    },
  },
  methods: {
    setLogin(status, username = ""){
      this.isLoggedIn = status;
      this.username = username;
    },
    startNewChat() {
      // 如果当前对话有内容，将其保存到历史记录
      if (this.chatHistory[this.activeChatIndex].length > 0) {
        this.chatHistory[this.activeChatIndex] = [...this.activeMessages];
      }

      // 创建一个新的空对话并设置为当前对话
      this.chatHistory.push([]); // 添加一个新对话到历史记录
      this.activeChatIndex = this.chatHistory.length - 1; // 设置新对话为当前对话
    },
    loadChatHistory(index) {
      // 切换到指定历史对话
      this.activeChatIndex = index;
    },
    updateMessages(messages) {
      // 更新当前对话的消息内容
      if (this.activeChatIndex >= 0) {
        this.chatHistory[this.activeChatIndex] = messages;
      }
    },
  },
};
</script>

