<template>
  <div class="auth-page">
    <h1>AI辅助就医问诊引擎</h1>
    <h2>登录</h2>
    <form @submit.prevent="login" class="auth-form">
      <input v-model="username" type="text" placeholder="用户名" required />
      <input v-model="password" type="password" placeholder="密码" required />
      <button type="submit">登录</button>
    </form>
    <p>
      没有账号？<button @click="navigateToRegister">注册</button>
    </p>
    <p>
      <button class="back-btn" @click="navigateToHome">返回主页</button>
    </p>
    <p v-if="message" :class="{ success: success, error: !success }">{{ message }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      message: "",
      success: false,
    };
  },
  methods: {
    login() {
      fetch("http://127.0.0.1:8000/login/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          username: this.username,
          password: this.password,
        }),
      })
        .then((res) => res.json())
        .then((data) => {
          if (data.success) {
            this.message = "登录成功";
            this.success = true;
            this.$router.push("/"); // 登录后跳转到首页
          } else {
            this.message = data.message;
            this.success = false;
          }
        })
        .catch(() => {
          this.message = "登录失败，请重试";
          this.success = false;
        });
    },
    navigateToRegister() {
      this.$router.push("/register");
    },
    navigateToHome() {
      this.$router.push("/");
    },
  },
};
</script>

<style>
.auth-page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  z-index: 999;
}

h1 {
  text-align: center;
  color: #3f51b5;
  margin-bottom: 1em;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 300px;
}

.auth-form input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.auth-form button {
  padding: 10px;
  font-size: 16px;
  border: none;
  background-color: #3f51b5;
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

button {
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
  border: none;
  background-color: #4CAF50; /* 绿色 */
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

.back-btn {
  background-color: #8BC34A; /* 浅绿色 */
}

.success {
  color: green;
}

.error {
  color: red;
}
</style>
