<template>
  <div class="home-page">
    <!-- 头部区域：登录状态 -->
    <header>
      <div class="header-content">
        <div v-if="isLoggedIn">
          <span>欢迎，{{ username }}</span>
          <button @click="logout">退出</button>
        </div>
        <div v-else>
          <span>未登录</span>
          <button @click="navigateToLogin">登录</button>
          <button @click="navigateToRegister">注册</button>
        </div>
      </div>
    </header>

    <!-- 标题 -->
    <h1>AI辅助就医问诊引擎</h1>

    <!-- 健康信息输入窗 -->
    <HealthInfo v-if="isLoggedIn" />

    <!-- 聊天窗口 -->
    <div class="chat-container">
      <div
        v-for="(message, index) in internalMessages"
        :key="index"
        :class="['chat-message', message.sender]"
      >
        <img :src="message.avatar" class="avatar" />
        <div class="message-content">{{ message.text }}</div>
      </div>
    </div>

    <!-- 输入框和发送按钮 -->
    <div class="input-container">
      <input
        v-model="userInput"
        @keyup.enter="sendMessage"
        placeholder="请输入您的问题..."
      />
      <button @click="sendMessage">发送</button>
    </div>
  </div>
</template>

<script>
import { inject } from "vue";
import HealthInfo from "./HealthInfo.vue"; // 引入健康信息组件

export default {
  props: ["messages"],
  components: {
    HealthInfo,
  },
  setup() {
    // 注入全局状态
    const isLoggedIn = inject("isLoggedIn", false); // 默认未登录
    const username = inject("username", ""); // 默认无用户名
    const setLogin = inject("setLogin");

    // 跳转到登录页面
    const navigateToLogin = () => {
      window.location.href = "/login"; // 简单跳转到登录页面，需配置路由
    };

    // 跳转到注册页面
    const navigateToRegister = () => {
      window.location.href = "/register"; // 简单跳转到注册页面，需配置路由
    };

    // 退出登录
    const logout = () => {
      setLogin(false, ""); // 重置登录状态
      window.location.reload();
    };

    return {
      isLoggedIn,
      username,
      navigateToLogin,
      navigateToRegister,
      logout,
    };
  },
  data() {
    return {
      internalMessages: [], // 内部消息记录
      userInput: "",
    };
  },
  watch: {
    messages: {
      immediate: true,
      handler(newMessages) {
        this.internalMessages = [...newMessages]; // 将 props 的消息复制到内部数据
      },
    },
  },
  methods: {
    sendMessage() {
      if (this.userInput.trim()) {
        const userMessage = {
          text: this.userInput,
          sender: "user",
          avatar: "../maomaochong.jpg", // 替换为实际头像路径
        };
        this.internalMessages.push(userMessage);
        this.userInput = "";

        // 模拟AI回复
        setTimeout(() => {
          const botMessage = {
            text: `这是对“${userMessage.text}”的自动回复。`,
            sender: "bot",
            avatar: "../maomaochong.jpg", // 替换为实际头像路径
          };
          this.internalMessages.push(botMessage);
          this.$emit("update-messages", this.internalMessages); // 将消息传递给父组件
        }, 1000);
      }
    },
  },
};
</script>

