<template>
  <div class="auth-page">
    <h1>AI辅助就医问诊引擎</h1>
    <h2>注册</h2>
    <form @submit.prevent="register" class="auth-form">
      <input v-model="username" type="text" placeholder="用户名" required />
      <input v-model="password" type="password" placeholder="密码（至少6个字符）" required />
      <input v-model="confirmPassword" type="password" placeholder="确认密码" required />
      <button type="submit">注册</button>
    </form>
    <p>
      已有账号？<button @click="navigateToLogin">登录</button>
    </p>
    <p v-if="message" :class="{ success: success, error: !success }">{{ message }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      confirmPassword: "",
      message: "",
      success: false,
    };
  },
  methods: {
    register() {
      if (this.password.length < 6) {
        this.message = "密码不能少于6个字符";
        this.success = false;
        return;
      }

      if (this.password !== this.confirmPassword) {
        this.message = "两次密码不一致";
        this.success = false;
        return;
      }

      fetch("http://127.0.0.1:8000/register/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          username: this.username,
          password: this.password,
        }),
      })
        .then((res) => res.json())
        .then((data) => {
          if (data.success) {
            this.message = "注册成功，请登录";
            this.success = true;
            this.$router.push("/login");
          } else {
            this.message = data.message;
            this.success = false;
          }
        })
        .catch(() => {
          this.message = "注册失败，请重试";
          this.success = false;
        });
    },
    navigateToLogin() {
      this.$router.push("/login");
    },
  },
};
</script>

<style>
.auth-page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  z-index: 999;
}

h1 {
  text-align: center;
  color: #3f51b5;
  margin-bottom: 1em;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 300px;
}

.auth-form input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.auth-form button {
  padding: 10px;
  font-size: 16px;
  border: none;
  background-color: #4CAF50; /* 绿色 */
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

button {
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
  border: none;
  background-color: #4CAF50; /* 绿色 */
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

.success {
  color: green;
}

.error {
  color: red;
}
</style>
